import "./App.css";
import { Canvas } from "@react-three/fiber";
import { CameraControls } from "@react-three/drei";
import ThreeDSurface from "./ThreeDSurface";
import { Sky } from "@react-three/drei";
import Axis from "./Axis";
import { useEffect, useRef } from "react";


// import ThreeDSurface from "./ThreeDSurface";

function App() {

  const camref = useRef();

  useEffect(()=>{
    // camref.current?.zoomTo(0.4,true);
    camref.current?.setLookAt( 6, 5, 10, 0,0,0, true );
  },[]);

  return (
    <div style={{ width: "100vw", height: "100vh" }}>
      <h1>  3D Grapher !! </h1>
      <Canvas camera={{position: [0,0,10]}}>
        <group rotation={[-Math.PI/2,0,0]}>

          <ambientLight intensity={0.5} />
          <spotLight
            position={[100, 100, 100]}
            angle={0.15}
            penumbra={1}
            castShadow={true}
          />
          <gridHelper rotation={[Math.PI/2,0,0]}/>
          <ThreeDSurface />
          <CameraControls ref={camref} polarAngle={1.1} azimuthAngle={-0.5}/>
          <Sky
            distance={450000}
            sunPosition={[5, 1, 8]}
            inclination={0}
            azimuth={0.25}
          />
          <Axis />
        </group>
      </Canvas>
    </div>
  );
}

export default App;
